/* Estilos base */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  background-color: #fff;
}
.header{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dark-mode-toggle{
  display: flex;
  align-items: baseline;
}
.board {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.column {
  flex: 1;
  background-color: #222;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.column h2 {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
  color: #ffffff;
}

.task-list {
  min-height: 200px;
  background-color: gray;
  margin-top: 10px;
  padding: 10px;
  border-radius: 8px;
  overflow-y: auto;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.add-task {
  margin-top: 10px;
  display: flex;
  gap: 10px;
}

.add-task input[type="text"] {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  color: #333333;
  background-color: #f2f2f2;
}

.add-task button {
  background-color: #61dafb;
  color: #ffffff;
  border: none;
  padding: 10px 16px;
  cursor: pointer;
  border-radius: 8px;
  font-size: 16px;
  transition: background-color 0.3s;
}

.add-task button:hover {
  background-color: #00b8d4;
}

.task {
  margin-bottom: 10px;
  background-color: #f9f9f9;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  cursor: move;
  transition: transform 0.3s;
}

.task:hover {
  transform: translateY(-4px);
}

.task h3 {
  margin: 0;
  font-size: 18px;
  font-weight: bold;
  color: #333333;
}

.task .delete-button {
  background-color: #ff4d4d;
  color: #ffffff;
  border: none;
  padding: 6px 12px;
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  transition: background-color 0.3s;
}

.task .delete-button:hover {
  background-color: #e63a3a;
}

.app.dark {
  background-color: #333;
  color: #fff;
  margin: 0
}

/* Estilos responsive */

/* 320px */
@media screen and (max-width: 320px) {
  .board {
    flex-direction: column;
    gap: 0;
  }
}

/* 425px */
@media screen and (max-width: 425px) {
  .column {
    padding: 16px;
  }

  .column h2 {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .task-list {
    min-height: 160px;
    padding: 8px;
  }

  .add-task input[type="text"] {
    padding: 8px;
    font-size: 14px;
  }

  .add-task button {
    padding: 8px 12px;
    font-size: 14px;
  }

  .task {
    padding: 12px;
  }

  .task h3 {
    font-size: 16px;
  }

  .task .delete-button {
    padding: 4px 8px;
    font-size: 12px;
  }
}

/* 768px */
@media screen and (max-width: 768px) {
  .board {
    flex-wrap: wrap;
  }

  .column {
    flex-basis: 100%;
    margin-bottom: 20px;
  }
}

/* 1024px */
@media screen and (max-width: 1024px) {
  .column {
    padding: 24px;
  }

  .column h2 {
    font-size: 28px;
    margin-bottom: 24px;
  }

  .task-list {
    min-height: 240px;
    padding: 16px;
  }

  .add-task input[type="text"] {
    padding: 12px;
    font-size: 18px;
  }

  .add-task button {
    padding: 12px 20px;
    font-size: 18px;
  }

  .task {
    padding: 20px;
  }

  .task h3 {
    font-size: 20px;
  }

  .task .delete-button {
    padding: 6px 12px;
    font-size: 16px;
  }
}

/* 1440px */
@media screen and (max-width: 1440px) {
  .column {
    padding: 32px;
  }

  .column h2 {
    font-size: 32px;
    margin-bottom: 28px;
  }

  .task-list {
    min-height: 280px;
    padding: 20px;
  }

  .add-task input[type="text"] {
    padding: 14px;
    font-size: 20px;
  }

  .add-task button {
    padding: 14px 24px;
    font-size: 20px;
  }

  .task {
    padding: 24px;
  }

  .task h3 {
    font-size: 24px;
  }

  .task .delete-button {
    padding: 8px 16px;
    font-size: 18px;
  }
}
